@charset "UTF-8";
@tailwind base;
@tailwind components;
@tailwind utilities;
/* UI Basic components */
.container {
  @apply max-w-screen-2xl mx-auto px-3;
}

.container-fuid {
  @apply max-w-full mx-auto px-3;
}

.icon-xs {
  @apply h-4 w-4;
}

.icon-sm {
  @apply h-5 w-5;
}

.icon-base {
  @apply h-7 w-7;
}

.icon-lg {
  @apply h-8 w-8;
}

.icon-xl {
  @apply h-9 w-9;
}

.btn {
  @apply inline-flex items-center rounded-md border border-transparent bg-primary px-4 py-2 text-base font-medium text-white hover:bg-primary-700 focus:outline-none focus:ring-2 focus:ring-focus focus:ring-offset-0 transition;
}

.btn-xs {
  @apply px-2.5 py-1.5 text-xs;
}

.btn-sm {
  @apply px-3 py-2 text-sm;
}

.btn-lg {
  @apply px-6 py-3 text-lg;
}

.btn-xl {
  @apply px-6 py-4 text-xl;
}

.btn-hollow {
  @apply bg-secondary text-primary border border-primary hover:bg-primary-100 focus:outline-none focus:ring-2 focus:ring-focus focus:ring-offset-0;
}

.btn-ghost {
  @apply bg-transparent text-primary border-none hover:bg-primary-100 focus:outline-none focus:ring-2 focus:ring-focus focus:ring-offset-0;
}

.btn-icon-only {
  @apply rounded-full;
}
.btn-icon-only.btn-xs {
  @apply p-1;
}
.btn-icon-only.btn-sm {
  @apply p-1.5;
}
.btn-icon-only.btn {
  @apply p-2;
}
.btn-icon-only.btn-lg {
  @apply p-2.5;
}
.btn-icon-only.btn-xl {
  @apply p-3;
}

.btn-icon .icon-xs {
  @apply -ml-0.5 mr-2;
}
.btn-icon .icon-sm {
  @apply -ml-1 mr-2;
}
.btn-icon .icon-base {
  @apply -ml-1 mr-2;
}
.btn-icon .icon-lg {
  @apply -ml-1.5 mr-3;
}
.btn-icon .icon-xl {
  @apply -ml-2 mr-4;
}

.menu.horizontal {
  @apply flex gap-8 items-baseline;
}
.menu li a {
  @apply inline-flex items-center text-base py-1 px-2 hover:bg-primary-50 rounded transition;
}
.menu li.current-menu-item a {
  @apply bg-white;
}
.menu.dropdown {
  @apply flex flex-col space-y-2;
}
.menu.dropdown li a {
  @apply w-full text-base py-1 px-2 hover:bg-primary-50 rounded transition;
}
.menu .dropdown-toggle {
  @apply inline-flex items-center text-base py-1 px-2 hover:bg-primary-50 rounded transition;
}

input[type=text],
input[type=email],
input[type=date] {
  @apply block w-full rounded-md border-gray-300 shadow-sm focus:border-focus-300 focus:ring focus:ring-focus-200 focus:ring-opacity-50;
}

select {
  @apply block w-full rounded-md border-gray-300 shadow-sm focus:border-focus-300 focus:ring focus:ring-focus-200 focus:ring-opacity-50;
}

textarea {
  @apply block w-full rounded-md border-gray-300 shadow-sm focus:border-focus-300 focus:ring focus:ring-focus-200 focus:ring-opacity-50;
}

input[type=checkbox] {
  @apply rounded border-gray-300 text-primary shadow-sm focus:border-focus-300 focus:ring focus:ring-offset-0 focus:ring-focus-200 focus:ring-opacity-50;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  @apply leading-tight mb-2;
}

h1 {
  @apply text-xl lg:text-3xl;
}

h2 {
  @apply text-lg lg:text-2xl;
}

h3 {
  @apply text-lg lg:text-xl;
}

h4 {
  @apply text-base lg:text-lg;
}

p {
  @apply leading-tight;
}

ol {
  @apply list-decimal pl-5;
}

ul:not(.menu) {
  @apply list-disc pl-5;
}

a.link {
  @apply underline hover:text-primary-300 transition;
}

.badge-primary {
  @apply bg-primary text-white py-0.5 px-2 rounded-full text-xs;
}

.badge-secondary {
  @apply bg-secondary text-black py-0.5 px-2 rounded-full text-xs border border-primary;
}

*:focus {
  @apply focus:outline-none focus:ring-2 focus:ring-focus focus:ring-offset-0;
}

/* ACF blocks */
.acf-block-accordion .accordion-item .accordion-title {
  border: none;
}
.acf-block-accordion .accordion-item .accordion-title::before {
  font-family: "Material Icons";
  content: "";
  font-size: 1.5rem;
  margin-top: -1rem;
}
.acf-block-accordion .accordion-item .accordion-title:focus, .acf-block-accordion .accordion-item .accordion-title:hover {
  background-color: none;
}
.acf-block-accordion .accordion-item .accordion-content {
  border: none;
}
.acf-block-accordion .accordion-item.is-active .accordion-title::before {
  content: "";
}

#editor .acf-block-accordion .accordion-item {
  list-style: none;
}
#editor .acf-block-accordion .accordion-item .accordion-title {
  text-decoration: none;
  border: none;
}
#editor .acf-block-accordion .accordion-item .accordion-title:before {
  display: none;
}

